.home-flex {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100vh;
}

.wlcm-text {
  color: #bcb8b1;
  width: 100%;
  height: 41%;
  align-items: flex-start;
  justify-content: center;
  display: flex;
  flex-direction: column;
  margin-bottom: 0;
  margin-left: 3vw;
}

.line1 {
  font-size: 8vw;
  font-weight: 60;
}

.line2 {
  font-size: 10vw;
  font-weight: 400;
  margin-bottom: 0;
}

.alg-btns {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background: radial-gradient(circle, #f4f3ee, #cad2c5);
  height: 59%;
}

.btn-1 {
  border: 1px solid #171a21;
  color: #171a21;
  background: transparent;
  text-decoration: none;
  padding: 0.5rem 1.5rem;
  border-radius: 4px;
  margin-top: 20px;
  width: 50%;
  justify-items: center;
  text-align: center;
  margin-bottom: 10px;
}
.btn-2 {
  border: 1px solid #171a21;
  color: #171a21;
  background: transparent;
  margin-top: 10px;

  text-decoration: none;
  padding: 0.5rem 1.5rem;
  border-radius: 4px;
  width: 50%;
  justify-items: center;
  text-align: center;
  margin-bottom: 10px;
}

@media (min-width: 768px) {
  .home-flex {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: calc(100vh - 6rem);
  }

  .wlcm-text {
    color: #bcb8b1;
    width: 70%;
    height: 100%;
    flex-basis: 67%;
    align-items: flex-start;
    justify-content: center;
    height: calc(100vh - 6rem);
    display: flex;
    flex-direction: column;
  }

  .line1 {
    font-size: 50px;
    font-weight: 60;
  }

  .line2 {
    font-size: 80px;
    font-weight: 400;
  }

  .alg-btns {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-basis: 30%;
    background: radial-gradient(circle, #f4f3ee, #cad2c5);
    height: calc(100vh - 6rem);
    border-radius: 20px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  }

  .btn-1 {
    border: 1px solid #171a21;
    color: #171a21;
    background: transparent;
    text-decoration: none;
    padding: 0.5rem 1.5rem;
    border-radius: 4px;
    margin-bottom: 15px;
    width: 50%;
    justify-items: center;
  }
  .btn-2 {
    border: 1px solid #171a21;
    color: #171a21;
    background: transparent;
    text-decoration: none;
    padding: 0.5rem 1.5rem;
    border-radius: 4px;
    margin-top: 15px;
    width: 50%;
    justify-items: center;
  }
  .btn-1:hover,
  .btn-1:active,
  .btn-2:hover,
  .btn-2:active {
    background: #22333b;
    border-color: #22333b;
    color: #f4f3ee;
  }
}
